
.logo {
    width: 12rem;
    height: 3rem;
}

.logo-align {
    width: 12rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
}

.text {
    font-size: 5rem;
    font-weight: 400;
}
.iconButton:hover .cartIcon{
    -webkit-animation: rotateIcon 2s 1;
            animation: rotateIcon 2s 1;
    color: #fdd700;
    cursor: pointer;
}

@-webkit-keyframes rotateIcon {
    0% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }

    100% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
}

@keyframes rotateIcon {
    0% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }

    100% {
        -webkit-transform: rotateY(180deg);
                transform: rotateY(180deg);
    }
}
